import React, { Component } from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import TracksList from './components/tracks'
import ModulesList from './components/lessons/list'
import LessonItem from './components/lessons/item'
import Slide from './components/lessons/slides/item'
import Login from './components/auth/login'
import Logout from './components/auth/logout'

import './style.css'

class Main extends Component{
  constructor(props){
    super(props)
    this.state = {
      showMenu: false
    }
  }

  requireAuth(nextState, replace) {
    if(typeof window.localStorage.access_token == 'undefined'){
      return <Redirect to="/front"/>
    } else {
      <this />
    }
  }

  showMenu(){
    this.setState({
      showMenu: !this.state.showMenu
    })
  }

  menuStyle(){
    if (this.state.showMenu) {
      return {
        right: 0,
        width: "200px"
      }
    } else {
      return {
        right: "-200px"
      }
    }
  }

  render(){
    return(
      <div>
        <header>
          <div className="main-nav">
            <div className="container">
              <div className="gomycode-title">
                <div className="whole-title">
                  <strong>GOMYCODE</strong> ONLINE ACADEMY
                </div>
                <div className="responsive-title">
                  <strong>GMC</strong> ONLINE ACADEMY
                </div>
                <div className="expand-menu-responsive" onClick={this.showMenu.bind(this)}>
                  <i className="fa fa-align-justify"></i>
                </div>
              </div>
              <div className="main-menu" style={this.menuStyle.call(this)}>
                <i onClick={this.showMenu.bind(this)} className="fa fa-times"></i>
                <ul>
                  <li>Dashboard</li>
                  <li>Tracks</li>
                  <li>Courses</li>
                  <li>Logout</li>
                </ul>
              </div>
            </div>
          </div>
        </header>
        <Switch>
          <Route exact path='/' component={TracksList}/>
          <Route exact path='/tracks' component={TracksList}/>
          <Route exact path='/subTracks/:id/modules' component={ModulesList}/>
          <Route exact path='/lessons/:id' component={LessonItem}/>
          <Route exact path='/slides/:id' component={Slide}/>
          <Route exact path='/login' component={Login}/>
          <Route exact path='/logout' component={Logout}/>
        </Switch>
      </div>
    )
  }
}

export default Main;



