import axios from 'axios'

const tracks = (state = [], action) => {
  if (action.type == "UPDATE_TRACKS_SUCCESS"){
    return action.payload.data.model
  }  else if (action.type == "UPDATE_TRACKS_FAIL") {
    return { error: "Anuthorized" }
  }
  return state || []
}

export default tracks



