const modules = (state = [], action) => {
  if(action.type == "UPDATE_MODULES_LIST_SUCCESS"){
    return action.payload.data.model.modules
  } else if (action.type == "UPDATE_MODULES_LIST_FAIL") {
    return { error: "Anuthorized" }
  }
  return state || []
}

export default modules



