import React, { Component } from 'react';
import './style.css';
import { connect } from 'react-redux'

import {updateTracksList} from '../../actions'

import { Link } from 'react-router-dom'


class TracksListComp extends Component{
  constructor(props){
    super(props);
    console.log(props.updateTracksList())
  }
  render(){
    return(
      <div className="TracksList">
        {
          this.props.tracks.map((track) => {
              return(
                <div key={track.id}>
                  <h2>
                    {track.name}
                  </h2>
                  <ul>
                    {
                      track.subTracks && track.subTracks.map( (level) => {
                        return <li key={level.id}><Link to={`/subTracks/${level.id}/modules`}>{ level.name }</Link></li>
                      })
                    }
                  </ul>
                </div>
              )
            }
          )
        }
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    tracks: state.tracks
  }
}

const mapDispatchToProps = dispatch => {
  return {
    updateTracksList: () => {
      dispatch(updateTracksList())
    }
  }
}


const TracksList = connect(
  mapStateToProps,
  mapDispatchToProps
)(TracksListComp)

export default TracksList;



