import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom'

import { Link } from 'react-router-dom'

class SlidesListComp extends Component{
  constructor(props){
    super(props)
    this.state = {
      slides: []
    }
  }
  componentWillReceiveProps(props){
    if(typeof props.currentLesson.slides == "undefined"){
      if(typeof props.currentSlide.lesson.slides != "undefined" ){
        let slides = props.currentSlide.lesson.slides
        slides.push({
          id: props.currentSlide.id,
          name: props.currentSlide.name,
          orderIndicator: props.currentSlide.orderIndicator
        })
        slides.sort(function(a, b){
          a.orderIndicator < b.orderIndicator
        })
        this.setState({
          slides
        })
      }
    }else{
      let slides = props.currentLesson.slides
      this.setState({
        slides
      })
    }
  }
  render(){
    return(
      <ul className="SlidesList">
        {
          this.state.slides &&
          this.state.slides.map( (slide) => {
            return (
              <div key={slide.id}>
              {
                slide.id == this.props.currentSlide.id &&
                <li className="active">
                  <Link to={"/slides/"+slide.id}>
                    {slide.name}
                  </Link>
                </li>
              }
              {
                slide.id != this.props.currentSlide.id &&
                <li>
                  <Link to={"/slides/"+slide.id}>
                    {slide.name}
                  </Link>
                </li>
              }
              </div>
            )
          })
        }
      </ul>
    )
  }
}

const mapStateToProps = state => {
  return {
    currentLesson: state.currentLesson,
    currentSlide: state.currentSlide
  }
}

const mapDispatchToProps = dispatch => {
  return {
  }
}


const SlidesList = connect(
  mapStateToProps,
  mapDispatchToProps
)(SlidesListComp)

export default SlidesList;



