import React, {Component} from 'react'
import axios from 'axios'
import { Redirect } from 'react-router-dom'

import './style.css'

const URL = "http://localhost:5069"

class Login extends Component{
  constructor(props) {
    super(props)
    this.state= {
      logedin: false
    }
  }

  auth(){
    axios.post(URL+"/api/authorize", {
      email: this.email.value,
      password: this.password.value
    }).then( (data) => {
      window.localStorage.access_token = data.data.token
      this.setState({
        logedin: true
      })
      console.log(this)
    }).catch( (err) => {
      console.log(err)
    })
  }

  render(){
    return(
      <div className="Login floating-box">
        {
          this.state.logedin &&
          <Redirect to='/' />
        }
        <div className="Login-header">
        </div>
        <div>
          <input className="form-control" name="email"  ref={(input) => { this.email = input; }} placeholder="email" />
        </div>
        <div>
          <input className="form-control" name="password" ref={(input) => { this.password = input; }} placeholder="password" type="password" />
        </div>
        <div>
          <button className="btn btn-success" onClick={ this.auth.bind(this) }>Login</button>
        </div>



        <div className="forget-password">
          Forget your password?
        </div>
        <div className="register-link">
          <button className="btn btn-primary">Register</button>
        </div>
      </div>
    )
  }
}


export default Login;



