import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom'

import SlidesList from '../list'

import { updateCurrentSlide }  from '../../../../actions'

class SlideItemComp extends Component{
  constructor(props){
    super(props)
    console.log(props)
    this.slide_id = props.match.params.id
    this.props.updateSlide(this.slide_id)
  }
  render(){
    return(
      <div className="SlideItem">
        {
          !this.props.currentSlide.error &&
          <Redirect to="/" />
        }
        <h1> { this.props.currentSlide.name } </h1>
        <div className="SlideItemMain">
          <div className="SlideItemContent">
            {
              this.props.currentSlide.content
            }
          </div>

          <div className="lessonSidebar">
            <SlidesList />
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    currentSlide: state.currentSlide
  }
}

const mapDispatchToProps = dispatch => {
  return {
    updateSlide: id => {
      dispatch(updateCurrentSlide(id))
    }
  }
}

const SlideItem = connect(
  mapStateToProps,
  mapDispatchToProps
)(SlideItemComp)

export default SlideItem;



